.product-more {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 25px;
  padding-right: 20px;
  padding-bottom: 24px;
  padding-left: 20px;

  border-right: 1px solid #e7e7e7;
  border-bottom: 1px solid #e7e7e7;
  border-left: 1px solid #e7e7e7;
}

.product-more__headline {
  margin: 0;
  margin-top: 18px;

  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
}

.product-more__descreption {
  margin: 0;
  margin-top: 8px;
  margin-bottom: 21px;

  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: #444444;
}

.product-more__button {
  padding-top: 10px;
  padding-right: 94px;
  padding-bottom: 10px;
  padding-left: 93px;

  color: #444444;

  background-color: #f2f2f2;

  cursor: pointer;

  &:hover {
    background-color: #ebebeb;
  }
}

@media (min-width: 768px) {
  .product-more {
    padding-top: 55px;
    padding-right: 37px;
    padding-bottom: 16px;
    padding-left: 37px;
    margin-top: 137px;
    width: 246px;

    border: 1px solid #cdcdcd;
  }

  .product-more__image img {
    width: 61px;
    height: 61px;
    opacity: 0.2;
  }

  .product-more__headline {
    margin-top: 22px;

    font-size: 20px;
    line-height: 20px;
    white-space: pre-line;
  }

  .product-more__descreption {
    margin-top: 9px;
    margin-bottom: 85px;
    font-size: 16px;
    line-height: normal;
    white-space: pre-line;
  }

  .product-more__button {
    padding-left: 78px;
    padding-right: 77px;
  }
}

@media (min-width: 1300px) {
  .product-more {
    margin-top: 136px;
    margin-right: 14px;
    padding-top: 51px;
    padding-bottom: 80px;

    width: 169px;
    height: 298px;
  }

  .product-more__image {
    margin-left: -3px;
    margin-top: 3px;
  }

  .product-more__headline {
    margin-left: -2px;
    margin-top: 43px;

    white-space: normal;
  }

  .product-more__descreption {
    margin-top: 27px;
    margin-left: -4px;

    white-space: normal;
  }

  .product-more__button {
    padding-left: 39px;
    padding-right: 39px;
  }
}
